.App {
  text-align: center;
}
/* NavigationBar CSS*/
.navbar-brand {
  font-size: 40px;
}

.navLink {
  font-size: 20px;
  font-family: "Sunflower", sans-serif;
  text-decoration: none;
  color: #8b8e8f;
}

.navLink:hover {
  color: gold;
}

.search-icon {
  background: none;
  border: none;
  color: #8b8e8f;
}

.search-icon svg {
  width: 25px;
  height: 25px;
}

.search-icon:hover {
  color: gold;
}

/* days Nav*/

.days_nav {
  background: #212529;
}

.daysLink_Btn {
  background: none;
  border: none;
  color: #8b8e8f;
  text-decoration: none;
  font-size: 34px;
  font-weight: 700;
  font-family: "Sunflower", sans-serif;
}

.daysLink_Btn:hover {
  color: gold;
}

.btn-active {
  background: white;
  color: black;
  border-radius: 10%;
}

.btn-active a {
  color: black;
}

.btn-active a:hover {
  color: black;
}

/* Nav bar CSS */

.nav-tabs {
  border: none;
  background: #212529;
}

.nav-item {
  font-weight: 600;
}

.nav-item a {
  color: white;
}

.All_Btn:hover {
  color: whitesmoke;
}

.Naver_Btn:hover {
  color: lawngreen;
}

.Kakao_Btn:hover {
  color: gold;
}

.Kakao-Page_Btn:hover {
  color: goldenrod;
}

/* Card CSS */
.toons {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-img-top {
  height: 230px;
}

.card_link {
  color: white;
  text-decoration: none;
}

.card_link:hover {
  color: gold;
}

.card-body {
  background: black;
}

.card-title {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-family: "Jua", sans-serif;
  font-size: 25px;
}

.additional {
  display: flex;
  position: absolute;
  top: 3px;
  left: 3px;
}

.additional-logo {
  padding: 2px;
}

.flatform-img {
  width: 2rem;
  border-radius: 25%;
  display: flex;
  justify-self: center;
  align-items: center;
  text-align: center;
}

.adult-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 25%;
  width: 2rem;
  height: 32px;
  background: red;
  color: white;
  font-weight: 800;
  border: 1px solid black;
}

.up-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 25%;
  width: 2rem;
  height: 32px;
  background: gold;
  color: black;
  font-weight: 700;
  border: 1px solid black;
  font-family: "Sunflower", sans-serif;
}

.new-logo {
  width: 6rem;
  position: absolute;
  left: 109px;
  top: -5px;
}

/* Recent CSS*/
.recent-edit {
  display: flex;
  justify-content: right;
  padding: 10px 10px 0px 0px;
}

.recent-edit button {
  border: none;
  background: none;
  font-size: 20px;
  font-weight: 400;
  font-family: "Jua", sans-serif;
}

.recent-none {
  color: black;
  font-family: "Jua", sans-serif;
  font-size: 25px;
}

.recent-del {
  position: absolute;
  top: 8px;
  right: 8px;
  border: none;
  background: none;
  color: red;
  font-size: 30px;
  font-weight: 600;
  font-family: "Jua", sans-serif;
}

/* Search CSS*/

.search-box {
  padding-top: 15px;
  background: #eeeeee;
  border-bottom: 2px solid black;
  position: fixed;
  z-index: 100;
  width: 100%;
  height: 60vh;
}

.search-input input {
  padding: 10px;
  padding-left: 15px;
  border: 2px solid black;
  width: 60vw;
  height: 45px;
  font-family: "Jua", sans-serif;
  border-radius: 0.5rem;
}

.closeBtn {
  position: absolute;
  top: 30px;
  right: 25px;
}

.search-result {
  height: 82.5%;
  overflow: auto;
}

.search-result-box {
  position: relative;
}

.searchToon {
  display: flex;
  justify-content: left;
  align-items: center;
  background: #444444;
  border-radius: 15px;
  color: white;
}

.searchToon-img {
  border: 1px solid black;
  border-radius: 0.5rem;
  background: #fedf25;
  width: 8rem;
  height: 158px;
}

.searchToon-detail p {
  margin: 0;
  padding: 3px 10px;
  display: flex;
  justify-content: left;
  align-items: center;
  font-family: "Jua", sans-serif;
}

.searchToon-detail a {
  margin: 0;
  padding: 3px 10px;
  display: flex;
  font-family: "Jua", sans-serif;
}

.searchToon-detail a button {
  border-radius: 0.5rem;
  color: black;
  margin: 0;
  padding: 1px 2px;
}

/* Loading CSS*/

.contentWrap {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%);
}
